export default "import React, {Component} from 'react';\r\nimport {DragDropContext, Draggable, Droppable} from 'react-beautiful-dnd';\r\nimport simpleListData from '../../../../@crema/services/db/extraPages/dndData/simpleListData';\r\nimport ListItemAvatar from '@material-ui/core/ListItemAvatar';\r\nimport Avatar from '@material-ui/core/Avatar';\r\nimport ListItemText from '@material-ui/core/ListItemText';\r\nimport ListItem from '@material-ui/core/ListItem';\r\nimport Box from '@material-ui/core/Box';\r\nimport {grey} from '@material-ui/core/colors';\r\nimport {Fonts} from '../../../../shared/constants/AppEnums';\r\nimport withStyles from '@material-ui/core/es/styles/withStyles';\r\n\r\n// a little function to help us with reordering the result\r\nconst reorder = (list, startIndex, endIndex) => {\r\n  const result = Array.from(list);\r\n  const [removed] = result.splice(startIndex, 1);\r\n  result.splice(endIndex, 0, removed);\r\n\r\n  return result;\r\n};\r\n\r\n/**\r\n * Moves an item from one list to another list.\r\n */\r\nconst move = (source, destination, droppableSource, droppableDestination) => {\r\n  const sourceClone = Array.from(source);\r\n  const destClone = Array.from(destination);\r\n  const [removed] = sourceClone.splice(droppableSource.compose, 1);\r\n\r\n  destClone.splice(droppableDestination.compose, 0, removed);\r\n\r\n  const result = {};\r\n  result[droppableSource.droppableId] = sourceClone;\r\n  result[droppableDestination.droppableId] = destClone;\r\n\r\n  return result;\r\n};\r\n\r\nconst grid = 8;\r\n\r\nconst styles = theme => {\r\n  return {\r\n    rootList: {\r\n      backgroundColor: theme.palette.background.paper,\r\n    },\r\n  };\r\n};\r\n\r\nconst getItemStyle = (isDragging, draggableStyle) => ({\r\n  // some basic styles to make the items look a bit nicer\r\n  userSelect: 'none',\r\n  padding: grid,\r\n  margin: `0 0 20px 0`,\r\n\r\n  // change background colour if dragging\r\n  background: isDragging ? 'lightgreen' : '#E0E0E0',\r\n\r\n  // styles we need to apply on draggables\r\n  ...draggableStyle,\r\n});\r\n\r\nconst getListStyle = isDraggingOver => ({\r\n  background: isDraggingOver ? 'lightblue' : 'lightgrey',\r\n  border: '5px solid #E0E0E0',\r\n  padding: grid,\r\n  marginRight: 10,\r\n  marginBottom: 20,\r\n  width: 300,\r\n  '@media (min-width: 600px)': {\r\n    width: 300,\r\n  },\r\n});\r\n\r\nclass MultipleDrop extends Component {\r\n  state = {\r\n    items: simpleListData.slice(0, 10),\r\n    selected: simpleListData.slice(5, 10),\r\n  };\r\n\r\n  /**\r\n   * A semi-generic way to handle multiple lists. Matches\r\n   * the IDs of the droppable container to the names of the\r\n   * source arrays stored in the state.\r\n   */\r\n  id2List = {\r\n    droppable: 'items',\r\n    droppable2: 'selected',\r\n  };\r\n\r\n  getList = id => this.state[this.id2List[id]];\r\n\r\n  onDragEnd = result => {\r\n    const {source, destination} = result;\r\n\r\n    // dropped outside the list\r\n    if (!destination) {\r\n      return;\r\n    }\r\n\r\n    if (source.droppableId === destination.droppableId) {\r\n      const items = reorder(\r\n        this.getList(source.droppableId),\r\n        source.index,\r\n        destination.index,\r\n      );\r\n\r\n      let state = {items};\r\n\r\n      if (source.droppableId === 'droppable2') {\r\n        state = {selected: items};\r\n      }\r\n\r\n      this.setState(state);\r\n    } else {\r\n      const result = move(\r\n        this.getList(source.droppableId),\r\n        this.getList(destination.droppableId),\r\n        source,\r\n        destination,\r\n      );\r\n\r\n      this.setState({\r\n        items: result.droppable,\r\n        selected: result.droppable2,\r\n      });\r\n    }\r\n  };\r\n\r\n  // Normally you would want to split things out into separate core.\r\n  // But in this example everything is just done in one place for simplicity\r\n  render() {\r\n    const {classes} = this.props;\r\n    return (\r\n      <DragDropContext onDragEnd={this.onDragEnd}>\r\n        <Box\r\n          display='flex'\r\n          flexDirection={{xs: 'column', sm: 'row'}}\r\n          justifyContent={{sm: 'center'}}\r\n          alignItems='center'>\r\n          <Droppable droppableId='droppable'>\r\n            {(provided, snapshot) => (\r\n              <Box\r\n                ref={provided.innerRef}\r\n                style={getListStyle(snapshot.isDraggingOver)}>\r\n                {this.state.items.map((item, index) => (\r\n                  <Draggable\r\n                    key={item.id}\r\n                    draggableId={item.handle}\r\n                    index={index}>\r\n                    {(provided, snapshot) => (\r\n                      <Box\r\n                        ref={provided.innerRef}\r\n                        {...provided.draggableProps}\r\n                        {...provided.dragHandleProps}>\r\n                        <Box px={2} bgcolor={grey[300]} mb={5} clone>\r\n                          <ListItem className={classes.rootList}>\r\n                            <Box\r\n                              mr={3}\r\n                              mt={0}\r\n                              ml={1.5}\r\n                              display='flex'\r\n                              justifyContent='center'\r\n                              clone>\r\n                              <ListItemAvatar>\r\n                                <Avatar alt='Remy Sharp' src={item.image} />\r\n                              </ListItemAvatar>\r\n                            </Box>\r\n                            <Box my={0} clone>\r\n                              <ListItemText\r\n                                primary={\r\n                                  <Box component='span' fontFamily={Fonts.BOLD}>\r\n                                    {item.name}\r\n                                  </Box>\r\n                                }\r\n                                secondary={\r\n                                  <Box\r\n                                    component='span'\r\n                                    display='block'\r\n                                    color='text.secondary'\r\n                                    overflow='hidden'\r\n                                    textOverflow='ellipsis'\r\n                                    whiteSpace='nowrap'>\r\n                                    @{item.handle}\r\n                                  </Box>\r\n                                }\r\n                              />\r\n                            </Box>\r\n                          </ListItem>\r\n                        </Box>\r\n                      </Box>\r\n                    )}\r\n                  </Draggable>\r\n                ))}\r\n                {provided.placeholder}\r\n              </Box>\r\n            )}\r\n          </Droppable>\r\n          <Droppable droppableId='droppable2'>\r\n            {(provided, snapshot) => (\r\n              <Box\r\n                ref={provided.innerRef}\r\n                style={getListStyle(snapshot.isDraggingOver)}>\r\n                {this.state.selected.map((item, index) => (\r\n                  <Draggable key={item.id} draggableId={item.id} index={index}>\r\n                    {(provided, snapshot) => (\r\n                      <Box\r\n                        ref={provided.innerRef}\r\n                        {...provided.draggableProps}\r\n                        {...provided.dragHandleProps}\r\n                        style={getItemStyle(\r\n                          snapshot.isDragging,\r\n                          provided.draggableProps.style,\r\n                        )}>\r\n                        <Box p={0} clone>\r\n                          <ListItem className={classes.rootList}>\r\n                            <Box\r\n                              mr={3}\r\n                              mt={0}\r\n                              ml={1.5}\r\n                              display='flex'\r\n                              justifyContent='center'\r\n                              clone>\r\n                              <ListItemAvatar>\r\n                                <Avatar alt='Remy Sharp' src={item.image} />\r\n                              </ListItemAvatar>\r\n                            </Box>\r\n                            <Box my={0} clone>\r\n                              <ListItemText\r\n                                primary={\r\n                                  <Box component='span' fontFamily={Fonts.BOLD}>\r\n                                    {item.name}\r\n                                  </Box>\r\n                                }\r\n                                secondary={\r\n                                  <Box\r\n                                    component='span'\r\n                                    display='block'\r\n                                    color='text.secondary'\r\n                                    overflow='hidden'\r\n                                    textOverflow='ellipsis'\r\n                                    whiteSpace='nowrap'>\r\n                                    @{item.handle}\r\n                                  </Box>\r\n                                }\r\n                              />\r\n                            </Box>\r\n                          </ListItem>\r\n                        </Box>\r\n                      </Box>\r\n                    )}\r\n                  </Draggable>\r\n                ))}\r\n                {provided.placeholder}\r\n              </Box>\r\n            )}\r\n          </Droppable>\r\n        </Box>\r\n      </DragDropContext>\r\n    );\r\n  }\r\n}\r\n\r\nexport default withStyles(styles)(MultipleDrop);\r\n";